@use '../../styles/variables.scss';
.info {
  width: 400px;
  height: 40px;
  background: variables.$light-purple;
  position: absolute;
  left: calc(50% - 200px);
  top: -30px;
  border: 4px solid variables.$error-red;
  border-radius: 4px;
  text-align: center;
  line-height: 30px;
  font-weight: 600;
  visibility: hidden;
  opacity: 0;
}
.info-show {
  visibility: visible;
  opacity: 1;
  transition: all 1s ease-in-out;
  top: 30px;
}